import gsap from "gsap";
import {
    showLoadingSpinner,
    hideLoadingSpinner,
} from "../templates/template-creator";
import SplitText from "gsap-trial/dist/SplitText";

const Home = {
    async render() {
        return `
            <hero-content></hero-content>
            <about-content></about-content>
            <fitur-content></fitur-content>
            <testimonial-content></testimonial-content>
            <qna-content></qna-content>
            <getstarted-content></getstarted-content>
            <footer-content></footer-content>
      `;
    },

    async afterRender() {
        showLoadingSpinner();

        // add navbar
        const navbar = document.querySelector("navbar-content");
        navbar.style.display = "block";

        // navbar scroll down hidden 
        let lastScrollY = window.scrollY;
        window.addEventListener("scroll", () => {
            if (lastScrollY < window.scrollY) {
                navbar.classList.add("navbar-hidden")
            } else {
                navbar.classList.remove("navbar-hidden")
            }

            lastScrollY = window.scrollY;

            if (window.scrollY <= 0) {
                navbar.classList.remove('navbar-hidden')
            }
        });

        // faq
        const faqElements = document.querySelectorAll('.content-item');

        faqElements.forEach((faq) => {
            faq.addEventListener('click', () => {
                faq.classList.toggle('active');
            });
        });

        // navbar scroll active menu
        const sections = document.querySelectorAll('section');
        const navLinks = document.querySelectorAll('header nav a');

        window.onscroll = () => {
            const scrollY = window.scrollY;

            sections.forEach(sec => {
                const offset = sec.offsetTop - 150;
                const height = sec.offsetHeight;
                const id = sec.getAttribute('id');
                const link = document.querySelector('header nav a[href="#' + id + '"]');

                if (scrollY >= offset && scrollY < offset + height) {
                    navLinks.forEach(link => {
                        link.classList.remove('active');
                    });
                    if (link) {
                        link.classList.add('active');
                    }
                }
            });
        };

        // gsap animation hero
        let isAnimationPlayed = false;

        // Fungsi untuk memulai animasi
        function animateContent() {

            // Reset visibility
            gsap.set('.text-content, .img-content', { clearProps: 'all' });

            if (window.innerWidth <= 874 && !isAnimationPlayed) {
                // Animasi untuk img-content muncul lebih dulu di layar tablet
                gsap.fromTo('.hero-container .img-content', {
                    y: 30,
                    opacity: 0
                }, {
                    y: 0,
                    opacity: 1,
                    duration: 1.5,
                    ease: 'power4.out'
                });

                gsap.fromTo('.hero-container .text-content', {
                    y: 30,
                    opacity: 0
                }, {
                    y: 0,
                    opacity: 1,
                    duration: 1.5,
                    ease: 'power4.out'
                });

                gsap.fromTo('.hero-container .text-wrapper h1', {
                    y: 30,
                    opacity: 0
                }, {
                    y: 0,
                    opacity: 1,
                    duration: 1,
                    delay: 0.5,
                    ease: 'power4.out'
                });

                gsap.fromTo('.hero-container .text-wrapper p', {
                    y: 30,
                    opacity: 0
                }, {
                    y: 0,
                    opacity: 1,
                    duration: 1,
                    delay: 0.7,
                    ease: 'power4.out'
                });

                gsap.fromTo('.hero-container .btn', {
                    scale: 0,
                    opacity: 0
                }, {
                    scale: 1,
                    opacity: 1,
                    duration: 1,
                    delay: 1,
                    ease: 'back.out(1.7)'
                });

                isAnimationPlayed = true; // Set variabel isAnimationPlayed menjadi true setelah animasi dijalankan
            } else if (window.innerWidth > 874 && !isAnimationPlayed) { // Tambahkan kondisi untuk desktop
                // Animasi untuk desktop
                gsap.fromTo('.hero-container .text-content', {
                    y: 30,
                    opacity: 0
                }, {
                    y: 0,
                    opacity: 1,
                    duration: 1.5,
                    ease: 'power4.out'
                });

                gsap.fromTo('.hero-container .text-wrapper h1', {
                    y: 30,
                    opacity: 0
                }, {
                    y: 0,
                    opacity: 1,
                    duration: 1,
                    delay: 0.5,
                    ease: 'power4.out'
                });

                gsap.fromTo('.hero-container .text-wrapper p', {
                    y: 30,
                    opacity: 0
                }, {
                    y: 0,
                    opacity: 1,
                    duration: 1,
                    delay: 0.7,
                    ease: 'power4.out'
                });

                gsap.fromTo('.hero-container .btn', {
                    scale: 0,
                    opacity: 0
                }, {
                    scale: 1,
                    opacity: 1,
                    duration: 1,
                    delay: 1,
                    ease: 'back.out(1.7)'
                });

                gsap.fromTo('.hero-container .img-content', {
                    y: 30,
                    opacity: 0
                }, {
                    y: 0,
                    opacity: 1,
                    duration: 1.5,
                    delay: 1.5,
                    ease: 'power4.out'
                });

                isAnimationPlayed = true; // Set variabel isAnimationPlayed menjadi true setelah animasi dijalankan
            }
        }

        // Jalankan animasi saat halaman dimuat
        animateContent();

        // Fungsi debounce untuk menjalankan animasi saat ukuran layar berubah
        let resizeTimeout;
        window.addEventListener('resize', () => {
            if (resizeTimeout) {
                clearTimeout(resizeTimeout);
            }
            resizeTimeout = setTimeout(() => {
                animateContent();
            }, 200); // Gunakan debounce untuk membatasi frekuensi pemanggilan animasi saat resize
        });

        hideLoadingSpinner();
    },
};

export default Home;